.vehicle-background {
  background-attachment: fixed;
  background-size: cover;
  width: 100%;
}

.vehicle-title-bg {
  padding-bottom: 40%;
  background-image: linear-gradient(black, transparent 40%);
  opacity: .4;
  z-index: 1;
  position: relative;
}

.vehicle-title {
  padding-top: 3%;
  text-align: center;
  color: white;
  font-size: 25pt;
  font-weight: 400;
}

.vehicle-position {
  margin-top: -40%;
  padding-bottom: 35%;
  position: relative;
  z-index: 20;
}

.vehicle-subtitle {
  text-align: center;
  color: white;
  font-size: 23pt;
  font-weight: 300;
}

.specs-overlay {
  width: 100%;
  height: 450px;
  background: rgba(35, 35, 35, 0.9);
}

.specs-details {
  margin-top: 2%;
  font-weight: 500;
  font-size: 15pt;
}

.specs-title {
  color: gray;
  font-weight: 400;
  font-size: 12pt;
}

.specs-button {
  height: 60px;
