  position: relative;
  top: 65%;
  left: 50%;
  margin-left: -180px;
  display: flex;
  justify-content: center;
  background-color: #0099ff;
  color: white;
  font-weight: 400;
  border-style: none;
}

.features {
  width: 100%;
  height: auto;
  padding-top: 7%;
  display: flex;
  align-items: center;
}

.features-text {
  width: 30%;
