}

.features-img {
  width: 70%;
  height: auto;
}

.features-img-fix {
  width: 100%;
  height: auto;
}

.spin-view {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  padding-top: 5%;
  padding-bottom: 5%;
}

.spin-title {
  color: black;
  text-align: center;
  font-size: 28pt;
  font-weight: 400;
  padding-top: 5%;
}

.interior-view {
  width: 100%;
  height: auto;
  position: relative;
}

.interior-view-img-fix {
  width: 100%;
  height: auto;
}

.interior-title {
  text-align: center;
  color: white;
  font-size: 25pt;
  font-weight: 400;
  position: absolute;
  top: 8%;
  width: 100%;
}

.video-box {
  width: 100%;
  height: auto;
}

.video-fix {
  position: relative;
  z-index: 3;
  width: 100%;
  height: auto;
}

.button-div {
  display: flex;
  justify-content: center;
}

.button-video1 {
  height: 80px;
  width: 45%;
  position: relative;
  z-index: 9;
