  font-size: 12pt;
  opacity: .8;
  border-style: none;
}

.outback {
  margin-top: -16%;
  width: 100%;
  height: auto;
  background-color: #333333;
  position: relative;
  z-index: 11;
  /*display: flex;
  justify-content: center;*/
}

.outback-title {
  text-align: center;
  font-style: italic;
  color: white;
  font-size: 23pt;
  font-weight: 300;
  position: absolute;
  z-index: 4;
  top: 14%;
  width: 100%;
  text-shadow: 1px 2px 7px black;
}

.outback-info {
  font-size: 18pt;
  font-weight: 300;
  color: white;
  margin-top: 4%;
  display: flex;
  flex-wrap: wrap;
  height: auto;
  width: 60%;
  text-align: center;
  border-style: solid;
  border-bottom: 2px solid #515151;
  padding-bottom: 4%;
}

.outback-desc {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
}

.outback-desc-pos {
  width: 60%;
  height: inherit;
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid #515151;
  padding-bottom: 4%;
}

.outback-font1 {
  color: white;
  font-size: 11pt;
  font-weight: 300;
  width: 50%;
  height: inherit;
  padding-right: 3%;
  padding-top: 3%
}

.outback-font2 {
  color: white;
  font-size: 11pt;
  font-weight: 300;
  width: 50%;
  height: inherit;
  padding-left: 3%;
  padding-top: 3%;
}

.outback-heading {
  color: white;
  font-size: 13pt;
  font-weight: 400;
  margin-bottom: 5%;
  margin-top: 10%;
}

.outback-title-font {
  font-size: 25pt;
  color: white;
  width: 100%;
  text-align: center;
  font-weight: 400;
  margin-bottom: 5%;
  margin-top: 4%;
}

.new-outback {
  color: #0099FF;
  font-size: 14pt;
  font-weight: 400;
  margin-left: 2%;
}


